.footer{
    background-color: #3f4f6d;
    font-size: 18px;
    color: white;
    padding: 30px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.footer div{
    width: 270px;
}

.created{
    text-align: left;
    padding-left: 15px;
    font-size: 24px;

    span {
        font-size: 18px;
    }
}

.developersList{
    display: flex;
    list-style-type: none;
    justify-content: space-between;
}

.developer {
    margin: 5px 10px;
}

.developer a:hover{
    opacity: 0.6;
}

.year {
    font-size: 18px;
}

.rslogo:hover{
    opacity: 0.6;
}